import "react-responsive-carousel/lib/styles/carousel.min.css"
import React from "react"
import { images } from "../components/Laboratorio/Imagenes"
import {
  bootstrapImg,
  cssImg,
  htmlImg,
  jqueryImg,
  mysqlImg,
  phpImg,
  vueImg,
} from "../components/Home/Icons"
import LayoutProyecto from "../components/Layout/Proyecto"

const Carousel = require("react-responsive-carousel").Carousel

const laboratorio = () => {
  const icons = [
    { src: phpImg, alt: "php" },
    { src: mysqlImg, alt: "mysql" },
    { src: vueImg, alt: "vue" },
    { src: jqueryImg, alt: "jquery" },
    { src: cssImg, alt: "css" },
    { src: htmlImg, alt: "html" },
    { src: bootstrapImg, alt: "bootstrap" },
  ]
  return (
    <LayoutProyecto
      title="Laboratorio de Análisis clínico"
      subtitle="Sistema de gestión de análisis clínicos"
      description="Imágenes ilustrativas del sistema que se creo para gestionar análisis clínicos de un laboratorio privado el cual cuenta roles de usuario, administrador de pacientes, medicos, responsables, capturistas, análisis, parámetros, estadísticas, bitácora y perfil de usuario"
      icons={icons}
      images={images}
    />
  )
}

export default laboratorio
